import { subTypeToI18nKeyMap, ruleTypeToI18nKeyMap } from './constant'
export default {
  computed: {
    subTypeOptions() {
      return Object.entries(subTypeToI18nKeyMap).map(([key, i18nKey]) => {
        const translatedText = this.$t(i18nKey)
        return {
          value: Number(key),
          label: translatedText,
        }
      })
    },
    ruleTypeOptions() {
      return Object.entries(ruleTypeToI18nKeyMap).map(([key, i18nKey]) => {
        const translatedText = this.$t(i18nKey)
        return {
          value: Number(key),
          label: translatedText,
        }
      })
    },
  },
}
