<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item :label="$t('rule.ruleName')" prop="ruleName">
                  <el-input
                    v-model="qryParam.ruleName"
                    :placeholder="$t('rule.enter') + $t('rule.ruleName')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('rule.supplier')" prop="supplierNbr">
                  <ct-async-select
                    v-model="qryParam.supplierNbr"
                    :placeholder="selectPh('rule.supplier')"
                    url="/prodInst/getSupplierListForQuery"
                    method="get"
                    label-key="attrValueName"
                    value-key="attrValue"
                  ></ct-async-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('rule.ruleCategory')" prop="ruleType">
                  <ct-select
                    v-model="qryParam.ruleType"
                    :placeholder="$t('rule.pleaseSelect')"
                    :options="ruleTypeOptions"
                  />
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('rule.ruleType')" prop="ruleSubType">
                  <ct-select
                    v-model="qryParam.ruleSubType"
                    :placeholder="$t('rule.pleaseSelect')"
                    :options="subTypeOptions"
                  />
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('rule.triggerTime')" prop="beginSendTime">
                    <ct-date-picker
                      :begin.sync="qryParam.beginSendTime"
                      :end.sync="qryParam.endSendTime"
                    >
                    </ct-date-picker>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('rule.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('rule.resetting')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
        <div class="tool-button-group">
          <BatchExport
            :query-id="2060"
            :params="qryParam"
            :show-msg="tableData.length === 0"
          ></BatchExport>
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :element-loading-text="$t('rule.loading')"
        :data="tableData"
        :border="true"
        overflow-x="scroll"
        height="100%"
      >
        <el-table-column
          prop="ruleName"
          :label="$t('rule.ruleName')"
          show-overflow-tooltip
          min-width="80"
        >
          <!-- <template slot-scope="scope">
            <a class="cor-blue" @click="goDetail(scope.row)">{{ scope.row.ruleName }}</a>
          </template> -->
        </el-table-column>
        <!-- <el-table-column
          prop="ruleId"
          :label="$t('rule.ruleId')"
          show-overflow-tooltip
          min-width="70"
        ></el-table-column> -->
        <el-table-column
          prop="supplierNbrDesc"
          :label="$t('rule.supplier')"
          show-overflow-tooltip
          min-width="80"
        ></el-table-column>
        <el-table-column
          prop="ruleType"
          :label="$t('rule.ruleCategory')"
          show-overflow-tooltip
          min-width="70"
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="ruleSubType"
          :label="$t('rule.ruleType')"
          show-overflow-tooltip
          min-width="70"
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="objType"
          :label="$t('rule.objectType')"
          show-overflow-tooltip
          min-width="70"
          :formatter="rowFormatter"
        >
        </el-table-column>
        <el-table-column
          prop="sendTime"
          :label="$t('rule.triggerTime')"
          show-overflow-tooltip
          min-width="80"
        ></el-table-column>
        <el-table-column
          prop="msisdn"
          label="MSISDN"
          show-overflow-tooltip
          min-width="80"
        ></el-table-column>
        <el-table-column
          prop="msgInfo"
          :label="$t('rule.pushMsg')"
          show-overflow-tooltip
          min-width="130"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { i18nKeyFromSubType, i18nKeyFromRuleType, i18nKeyFromObjType } from '../constant'
import langMixin from '@/mixins/langMixin'
import ruleMixin from '../ruleMixin'
import BatchExport from '@/components/BatchExport'
export default {
  components: { BatchExport },
  mixins: [langMixin, ruleMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      qryParam: {
        ruleName: '',
        supplierNbr: '',
        extCusId: '',
        ruleType: '',
        ruleSubType: '',
        // createTimeBegin: '', // 开始时间
        // createTimeEnd: '', // 结束时间
        beginSendTime: '', // 开始时间
        endSendTime: '', // 结束时间
      },
    }
  },
  mounted() {
    this.qryCmpParamPage(false)
  },
  methods: {
    goDetail(item) {
      this.$router.push({
        path: 'enforcementDetail',
        query: { custId: item.custId, recordId: item.recordId },
      })
    },
    formatRoleType(val) {
      return ''
    },
    qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        ...this.page,
      }
      this.loading = true
      this.$api.rule
        .qryRuleRecordList(param)
        .then((res) => {
          if (res.list && res.list.length > 0) {
            this.tableData = res.list
            this.page.total = res.total
          } else {
            this.tableData = []
            this.page.total = 0
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryCmpParamPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryCmpParamPage(true)
    },
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.qryCmpParamPage(false)
    },
    rowFormatter(row, col, val) {
      if (col.property === 'ruleType') {
        return this.$t(i18nKeyFromRuleType(row.ruleType)) || row.ruleTypeName
      } else if (col.property === 'ruleSubType') {
        return this.$t(i18nKeyFromSubType(row.ruleSubType)) || row.ruleSubTypeName
      } else if (col.property === 'objType') {
        if (!row.objTypeName) {
          return ''
        }
        return this.$t(i18nKeyFromObjType(row.objType)) || row.objTypeName
      } else if (col.property === 'extCustId') {
        return this.$i18n.locale === 'zh'
          ? row.supplierCustChineseName
          : row.supplierCustEnglishName
      }
      return val
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.action-button-group {
  display: flex;
  .el-button:first-child {
    padding-left: 0;
  }
}
</style>
